import React from 'react';
import PropTypes, { func } from 'prop-types';
import { ThemeProvider } from 'emotion-theming';
import { SEO } from '../components';
import Navigation from './layouts/Navigation/index';
import theme from '../../config/theme';
import Footer from './Footer';
import $ from 'jquery';

class Layout extends React.Component {
	
	componentDidMount(){
		var x = 0;
		var y = 0;
		var lastScrolledLeft = 0;
		var lastScrolledTop = 0;

	  var mouse_monitor = function(e) {
			x = e.pageX;
			y = e.pageY;
			setCursor()
			
		}

		function _hiddenScrollDown(){
			console.log('test')
		  var $sectionFirst = $('.js-first-section')
			var $scrollArrow = $('.scroll-down')

			if (!$sectionFirst.length) return;

			if($sectionFirst.get(0).getBoundingClientRect().top + 45 < 0) {
				$scrollArrow.addClass('hide')
			}else {
				$scrollArrow.removeClass('hide')
			}
		}

		var scrollMouse = function(){
			if(lastScrolledLeft != $(document).scrollLeft()){
				x -= lastScrolledLeft;
				lastScrolledLeft = $(document).scrollLeft();
				x += lastScrolledLeft;
		 }
		 if(lastScrolledTop != $(document).scrollTop()){
				y -= lastScrolledTop;
				lastScrolledTop = $(document).scrollTop();
				y += lastScrolledTop;
		 }
		}

		var setCursor = function(){
			var cursor = document.querySelector('.cursor-pointer')
			cursor.style.top = y + 'px';
			cursor.style.left = x + 'px';
		}
		



			//binding event on link
			$('.action-link').hover(function() {
				$('.cursor-pointer').addClass('active')
			}, function(){
				$('.cursor-pointer').removeClass('active')
			})

			$(document).ready( function ()
			{

					let elementScroll = $(window).width() > 460 ? document : 'body';
					$(elementScroll).scroll( function ()
					{
							console.log('scroll');
							scrollMouse()
							setCursor()
							_hiddenScrollDown()
					});
					
				 document.addEventListener('mousemove', mouse_monitor);
			});
	}


	render(){
		const { children } = this.props;
		return (
			<ThemeProvider theme={theme}>
				<React.Fragment>
					<SEO />
					<Navigation />
					{children}
					<div className="content content--demo-1">
						<div className="global-menu">
							<div className="global-menu__wrap">
								<a className="global-menu__item active" href="#">Me<span /></a>
								<a className="global-menu__item" href="#">Contact<span /></a>
								<a className="global-menu__item" href="#">Projects<span /></a>
								<a className="global-menu__item" href="#">Journal<span /></a>
							</div>
						</div>
						<svg className="shape-overlays" viewBox="0 0 100 100" preserveAspectRatio="none">
							<path className="shape-overlays__path"></path>
							<path className="shape-overlays__path"></path>
							<path className="shape-overlays__path"></path>
						</svg>
					</div>
					<Footer />
					<div className="cursor-pointer"></div>
				</React.Fragment>
			</ThemeProvider>
		);
	}
}

export default Layout;

Layout.propTypes = {
  children: PropTypes.oneOfType([PropTypes.array, PropTypes.node]).isRequired,
};
