import React from 'react';
import { Link } from 'gatsby';
import styled from 'react-emotion';


// Assets
import srcImgAvatar from '../../../favicon.png';
import Avatar from './avatar';

// Components
import ButtonHamburger from '../../commons/buttons/ButtonHamburger';

const Wrapper = styled.header`
  padding: 1em 2em;
  margin: 0 auto;
  position: fixed;
  width: 100%;
  z-index: 9;

  .container {
    align-items: center;
    display: flex;
    justify-content: space-between;

    a {
      line-height: 0;
    }
  }

  @media screen and (max-width: 767px){ 
    .container {
      width: 100%;
    }
  }
`;



const Navigation = () => (
  <Wrapper>
      <div className="container">
        <Link to="/"><Avatar src={srcImgAvatar} /></Link>
        <ButtonHamburger />
      </div>
  </Wrapper>
);

export default Navigation;
